.loader,
.loader:after {
 border-radius: 50%;
 width: 7em;
 height: 7em;
}
.loader {
 margin: auto auto;
 position: relative;
 text-indent: -9999em;
 border-top: 1.1em solid rgba(13, 12, 12, 0.2);
 border-right: 1.1em solid rgba(13, 12, 12, 0.2);
 border-bottom: 1.1em solid rgba(13, 12, 12, 0.2);
 border-left: 1.1em solid #0d0c0c;
 -webkit-transform: translateZ(0);
 -ms-transform: translateZ(0);
 transform: translateZ(0);
 -webkit-animation: load8 1.1s infinite linear;
 animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
 0% {
 -webkit-transform: rotate(0deg);
 transform: rotate(0deg);
 }
 100% {
 -webkit-transform: rotate(360deg);
 transform: rotate(360deg);
 }
}
@keyframes load8 {
 0% {
 -webkit-transform: rotate(0deg);
 transform: rotate(0deg);
 }
 100% {
 -webkit-transform: rotate(360deg);
 transform: rotate(360deg);
 }
}
.loader_Products {
 font-size: 8px;
}
.loader_Login {
 font-size: 2px;
}